.popup {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.popup.active{
    opacity: 1;
    pointer-events: all;
}

.popup__content {
    padding: 20px;
    border-radius: 12px;
    background-color: #fff;
    width: 50vw;
    height: 200px;
    transform: scale(0.5);
    transition: 0.4s all;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 26px;
}

.popup__content.active{
    transform: scale(1);
}